<template lang="pug">
    v-card 
        v-toolbar
            v-toolbar-title 用户信息
            
        v-list
            v-list-item
                v-list-item-content
                    v-list-item-title
                        h3 用户名

                    span {{username}}
            v-list-item
                v-list-item-content
                    v-list-item-title
                        h3 邮箱

                    span {{email}}

            v-list-item
                v-list-item-content
                    v-list-item-title
                        h3 TRX地址
                    span {{trxaddress}}

                    v-img(:src="trxaddressqrcode"
                    max-height="250" max-width="250")

                v-list-item-action
                    v-btn(color="success" dark @click="update_trxaddress") 获取地址

            v-list-item
                v-list-item-content
                    v-list-item-title
                        h3 到期时间
                    span {{expireData}}

                v-list-item-action
                    v-btn(color="success" dark @click="update_expiretime") 获取过期时间

                    
                
</template>

<script>
import { get} from 'vuex-pathify'

export default {
    data: () => ({
        
        
    }),
    computed: {
    isLogin:get('user/islogin'),
    username:get('user/username'),
    email:get('user/email'),
    trxaddress:get('user/trxaddress'),
    trxaddressqrcode:get('user/trxaddressqrcode'),
    exbotexpiretime:get('user/exbotexpiretime'),
    expireData(){
        return new Date(this.exbotexpiretime*1000).toISOString().substr(0, 10)

    }
    },
    methods:{
        update_trxaddress(){
            this.$store.dispatch("user/update_trxaddress");
        },
        update_expiretime(){
            this.$store.dispatch("user/update_exbot_expiretime");
        }
    }
}
</script>